import { useCallback, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { getLeague, getLeagues, updateLeagueStatus } from "../util/fetcher.js"
import { Button } from "@mui/material";

const League = () => {
  const {id} = useParams()
  const [activePage, setActivePage] = useState(false)
  const [league, setLeague] = useState({})
  const initLeague = useCallback(async() => {
    const fetchedLeagues = await getLeagues()
    setLeague(fetchedLeagues.find(fetchedLeague=>fetchedLeague.value === id)||{})
    setActivePage(true)
  }, [id, setLeague, setActivePage])
  const updateLeagueStatusAction = useCallback(async(active) => {
    setActivePage(false)
    await updateLeagueStatus(league.value, active)
    return initLeague()
  }, [initLeague, league, setActivePage])
  useEffect(()=> {
    initLeague()
  }, [id, initLeague])
  return (
    <div className="LeaguePage">
      {!league ? <h2>League page is loading...</h2> : (
        <>
          <h2>{league.name}</h2>
          <h3>Active: {league.active ? '✅' : '❌'} <Button disabled={!activePage} onClick={()=>updateLeagueStatusAction(league.active ? false : true)}>Change</Button></h3>
          <h3>Current Matchday: {league.currentMatchDay}</h3>
          <h4>Players: {league.players}, competition type: {league.isInternational ? 'Nations' : 'Clubs'}</h4>
        </>
      )}
    </div>
    )
}

export default League