import App from "./App"
import Leagues from "./Components/Leagues"
import League from "./Components/League"
import Team from "./Components/Team"
import Teams from "./Components/Teams"

const routes = [{
  id: "default",
  path: "/",
  element: <App />,
  children:[{
    id: "leaguesapp",
    path: "leagues",
    element: <Leagues/>
  },{
    id: "league",
    path: "league/:id",
    element: <League />
  },{
    id: "teams",
    path: "teams",
    element: <Teams />
  },{
    id: "team",
    path: "team/:id",
    element: <Team />
  }]
}]

export default routes